// èŽ·å–æ¨¡æ¿ä¿¡æ¯
export const getConfigTemplate = () => {
  const tempPath = path.resolve(
    __dirname,

    '.' + path.sep + 'templates' + path.sep + 'config' + path.sep + 'init.json'
  )

  const tempData = fs.readFileSync(tempPath)
  return tempData.toString()
}

