  createServe: true, // ä¸ºæœåŠ¡å•ç‹¬åˆ›ç«‹æ–‡ä»¶å¤¹
  pathRoute: ['/admin/api/', '/'], // åœ¨ paths æ¨¡å¼ä¸­ï¼Œæˆªå–ä¸åŒæ–‡ä»¶çš„è·¯å¾„ï¼ˆé»˜è®¤åˆ†å‰²ä¸ºã€'/admin/api/', '/api/', '/'ã€‘ï¼‰
  excludeRoute: ['/api/'], // æŽ’é™¤é‚£äº›è·¯å¾„ä¸åšç”Ÿæˆå¤„ç†
  template: 'default', // æ¨¡æ¿ç±»åž‹ï¼Œç›®å‰åªæœ‰defaultç±»åž‹çš„æ¨¡æ¿ï¼Œæ˜¯åŸºäºŽå…¬å¸çš„PCç«¯è„šæ‰‹æž¶é…ç½®
