    dir: 'models', // æ¨¡åž‹æ·»åŠ çš„ç›®å½•ï¼Œç›¸å¯¹é¡¹ç›®srcæ¥è¯´
    isCover: false, // æ˜¯å¦è¦†ç›–ï¼Œå­˜åœ¨åˆ™ä¸åšå¤„ç†ï¼Œä¸å­˜åœ¨åˆ™åˆ›å»º
    commonResponse: 'itemResult', // å¯¹è±¡è¿”å›žæ¨¡åž‹
